/* === Lato - 100 */
@font-face {
	font-family: 'Lato';
	font-style: normal;
	font-weight: 100;
	src: url("./font/Lato-Thin.eot");
	src: local("Lato Hairline"),
		local("Lato-Hairline"),
		url("./font/Lato-Thin.eot") format("embedded-opentype"),
		url("./font/Lato-Thin.woff2") format("woff2"),
		url("./font/Lato-Thin.woff") format("woff"),
		url("./font/Lato-Thin.ttf") format("truetype"),
		url("./font/Lato-Thin.svg") format("svg");
}

